
.container {
    width: 80%;
    max-width: 800px;
    background: #2b2b3d;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: space-around;
    margin: 10px auto; /* Added margin to allow scrolling */
  }
  
  .fact {
    width: 30%;
    padding: 15px;
    background: #3a3a50;
    border-radius: 5px;
    text-align: center;
  }
  
  .fact h3 {
    margin: 0;
    font-size: 1.2em;
    color: #a29bfe;
  }
  
  .fact p {
    margin: 5px 0 0;
    font-size: 1.5em;
    font-weight: bold;
    color: #ffffff;
  }
  